/* Upload files Style */
/* === Wrapper Styles === */
#FileUpload {
  display: flex;
  justify-content: center;
}


/* === Upload Box === */
.upload {
  margin: 10px;
  height: 85px;
  border: 8px dashed #e6f5e9;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
}
.upload p {
  margin-top: 12px;
  line-height: 0;
  font-size: 22px;
  color: #0c3214;
  letter-spacing: 1.5px;
}
.upload__button {
  background-color: #e6f5e9;
  border-radius: 10px;
  padding: 0px 8px 0px 10px;
}
.upload__button:hover {
  cursor: pointer;
  opacity: 0.8;
}

.image-container{
  display: flex;
  align-items: center;
  justify-content: center;
}
.img-grid{
  max-width: 300px;
  max-height: 200px;
  min-width: 300px;
  min-height: 200px;
}
.input-file{
  padding: 20px;
}
/* END Upload files Style */
.imagen-inicio{
width: auto;
max-height: 125px;
}
.imagen-inicio-lista{
  width: auto;
  max-height: 50px;
}

.modal-pic{
  max-width: 450px;
  max-height: 400px;
}

.modal-dialog,
.come-from-modal.right .modal-dialog{
  position:fixed;
  height: 100%;
  transform: translate3d(0%, 0, 0);
}

.come-from-modal.right .modal-dialog{
    right: 0px;
    transition: opacity 0.3s linear, right 0.3 ease-out;
}
.modal-backdrop.show{
  opacity: 0.1 !important;
}

.modal-upload .modal-dialog{
  position: initial !important;
}
.table-item{
  cursor: pointer;
}
.sort-icon{
  cursor: pointer;
}